.wrap--nextgenthemes {
  max-width: 1100px;
  margin-right: auto;
  margin-left: auto;
}

.wp-core-ui .button-primary--ngt-small {
  height: 18px;
  padding-right: 5px;
  padding-left: 5px;
  line-height: 12px;
}

.nextgenthemes-label-text {
  display: block;
  margin-bottom: 3px;
  font-weight: 600;
}
[type=checkbox] + .nextgenthemes-label-text {
  display: inline;
}

.wrap--nextgenthemes__loading-indicator {
  width: 16px;
  height: 16px;
}

.ngt-settings-grid {
  display: -ms-grid;
  display: grid;
  grid-column-gap: 1rem;
      grid-template-areas: "content" "sidebar";
}
@media (min-width: 750px) {
  .ngt-settings-grid {
    grid-column-gap: 2rem;
        grid-template-areas: "content sidebar";
    -ms-grid-columns: 1fr 2rem 300px;
    grid-template-columns: 1fr 300px;
  }
}
@media (min-width: 1200px) {
  .ngt-settings-grid {
    grid-column-gap: 3rem;
    -ms-grid-columns: 1fr 420px;
    grid-template-columns: 1fr 420px;
  }
}

.ngt-settings-grid__content {
  -ms-grid-row: 1;
  -ms-grid-column: 1;
  grid-area: content;
}

.ngt-settings-grid__sidebar {
  -ms-grid-row: 2;
  -ms-grid-column: 1;
  grid-area: sidebar;
}

@media (min-width: 750px) {
  .ngt-settings-grid__content {
    -ms-grid-row: 1;
    -ms-grid-column: 1;
  }
  .ngt-settings-grid__sidebar {
    -ms-grid-row: 1;
    -ms-grid-column: 3;
  }
}

.ngt-sidebar-box {
  background: #fff;
  border-left: 4px solid #46b450;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.1);
  margin-bottom: 1rem;
  padding: 1rem;
}
.ngt-sidebar-box h3:first-child {
  margin-top: 0;
}
.ngt-sidebar-box ul {
  list-style: square;
  padding-left: 1em;
}
.ngt-sidebar-box li {
  padding-left: 0.7em;
  margin-bottom: 1em;
}
@media (min-width: 1200px) {
  .ngt-sidebar-box {
    padding: 3rem;
  }
}